export class AccommodationSearch{
    city: string;
	beginDate: string;
	endDate: string;
	numberOfPeople: number;
	advance: boolean;
	accomodationtype: string;
	category: number;
    additionalService: string[];
    orderByValue: string;
    
    constructor(){
        this.city = "";
        this.beginDate = "";
        this.endDate = "";
        this.numberOfPeople = 1;
        this.advance = false;
        this.accomodationtype = "";
        this.category = 0;
        this.additionalService = [];
        this.orderByValue = "NONE"
    }
	
}