import { Component, OnInit } from '@angular/core';
import { Accommodation } from 'app/model/accommodation/accommodation';
import { Review } from 'app/model/user/review';
import { Message } from 'app/model/user/message';
import { UserService } from 'app/service/user/user.service';
import { User } from 'app/model/user/user';
import { ChatService } from 'app/service/chat/chat.service';
import { AuthService } from 'app/service/user/auth.service';
import { AccommodationService } from 'app/service/accommodation/accommodation.service';
import { AccommodationReservation } from 'app/model/accommodation/accommodationReservation';
import { UserReviewService } from 'app/service/userReview/user-review.service';
import { elementAt } from 'rxjs/operators';

@Component({
  selector: 'app-my-reservation',
  templateUrl: './my-reservation.component.html',
  styleUrls: ['./my-reservation.component.css']
})
export class MyReservationComponent implements OnInit {

  selectedRate: number = -1;
  selectedTextBox: number = -1;
  selectedTextBoxMessage: number = -1;
  newReview: Review = new Review();
  newMessage: Message = new Message();
  accommodationToShow: AccommodationReservation[] = [];
  userReviews: Review[] = [];
  pomReviews: Review[] = [];
  currentRate = 4;
  loggedUser: User = new User();

  constructor(private accommodationService: AccommodationService, private userService: UserService, private chatService: ChatService, private authService: AuthService,
              private userReviewService: UserReviewService) { 
    let res = localStorage.getItem('token');
    if(res != null){
      this.loggedUser.email = this.authService.getUsername(res);
    }
  }

  ngOnInit() {

    this.userService.findByEmail(this.loggedUser.email).subscribe(
      e => {
        this.loggedUser = e;
        this.accommodationService.getUserReservations(this.loggedUser.id).subscribe(
          s => {
            this.accommodationToShow = s;
            this.accommodationToShow.forEach(element =>
              {
                let newReview = new Review();
                newReview.roomDTO = element.roomDTO;
                newReview.systemUserInfoDTO = this.loggedUser;
                this.pomReviews.push(newReview);
              })
              this.userReviewService.getUserReviews(this.loggedUser.id).subscribe(
                s => {
                  this.userReviews = s;
                  this.userReviews.forEach(userReview => {
                    this.pomReviews.forEach(element => {
                      if(element.roomDTO.id == userReview.roomDTO.id){
                        element.id = userReview.id;
                        element.comment = userReview.comment;
                        element.rating = userReview.rating;
                      }
                    })
                  })
                }
              )
          }
        )
      })

  }

  addComment(accommodation: AccommodationReservation){
    if(this.selectedTextBox != -1 && this.selectedTextBox == accommodation.id){
      this.selectedTextBox = -1;
    }
    else{
      this.selectedTextBox = accommodation.id;
    }
    
    this.newReview = new Review();
  }

  over(idAccommodation: AccommodationReservation){
    if(idAccommodation.realised)
      this.selectedRate = idAccommodation.id;
  }

  out(){
    this.selectedRate = -1;
  }

  showMessageArea(accommodation: AccommodationReservation){
    this.selectedTextBoxMessage = accommodation.id;
  }

  sendMessage(accommodation: AccommodationReservation){
    if(this.newMessage.caption == ""){
      alert("Please enter message caption")
    }
    else if(this.newMessage.text == ""){
      alert("Message is empty")
    }
    this.newMessage.date = new Date();
    this.newMessage.opened = false;
    this.newMessage.sender = this.loggedUser;
    this.chatService.sendMessage(-1, accommodation.roomDTO.hotelDTO.id, this.newMessage).subscribe(
      s => {
        alert("Poruka je poslata");
        this.selectedTextBoxMessage = -1;
      }
    )
  }

  cancel(accommodation: AccommodationReservation){
    if(confirm("Cancel accommodation reservation?")) {
    this.accommodationService.cancel(accommodation.id).subscribe(
      s => {
        let pomReservation: AccommodationReservation = new AccommodationReservation();
        this.accommodationToShow.forEach(element => {
          if(element.id === pomReservation.id){
            pomReservation = element;
          }
        })

        this.accommodationToShow.splice(this.accommodationToShow.indexOf(pomReservation), 1)
      }
    )
    }
  }

  saveRate(review: Review){
    if(this.newReview.comment != ""){
      review.approved = false;
      review.comment = this.newReview.comment;
      this.newReview = new Review();
    }
    if(review.id == 0){
      this.userReviewService.createReview(review).subscribe(
        s => {
          this.pomReviews.forEach(element => {
            if(element.roomDTO.id == s.roomDTO.id){
              element.id = s.id;
              element.rating = s.rating;
              element.comment = s.comment;
            }
          })

          this.accommodationService.updateRating(s.roomDTO.id).subscribe(
            s => {
              console.log("Uspeo");
            }
          );
        }
      )
    }
    else{
      this.userReviewService.updateReview(review).subscribe(
        s => {
          this.pomReviews.forEach(element =>{
            if(element.roomDTO.id == s.roomDTO.id){
              element.rating = s.rating;
              element.comment = s.comment;
            }
          })
          this.accommodationService.updateRating(s.roomDTO.id).subscribe(
            s => {
              console.log("Uspeo");
            }
          );
        }
      )
    }
    this.selectedTextBox = -1;
  }

}
