import { Component, OnInit, ViewChild } from '@angular/core';
import { AccommodationSearch } from 'app/model/accommodation/accommodationSearch';
import { NgbTypeahead } from '@ng-bootstrap/ng-bootstrap';
import {debounceTime, distinctUntilChanged, filter, map} from 'rxjs/operators';
import { Observable } from 'rxjs';
import { AccommodationService } from 'app/service/accommodation/accommodation.service';
import { Router, NavigationExtras, ActivatedRoute } from '@angular/router';
import { NgbDate } from '@ng-bootstrap/ng-bootstrap/datepicker/ngb-date';
import { Accommodation } from 'app/model/accommodation/accommodation';
import { AddressService } from 'app/service/address/address.service';
import { getAllDebugNodes } from '@angular/core/src/debug/debug_node';
import { Address } from 'app/model/global-parameters/address';
import { HotelService } from 'app/service/hotel/hotel.service';
import { Hotel } from 'app/model/accommodation/hotel';
import { Image } from 'app/model/global-parameters/image';
import { UploadFileService } from 'app/service/image/upload-file.service';


@Component({
  selector: 'app-view-address',
  templateUrl: './view-address.component.html',
  styleUrls: ['./view-address.component.css']
})
export class ViewAddressComponent implements OnInit {
  
  p: number;
  accommodationSearch: AccommodationSearch = new AccommodationSearch();
  beginDate: NgbDate;
  endDate: NgbDate;
  addressToShow: Address[] = [];
  states: string[] = [];
  default: Image ;
  hotels: Image[] = [];

  search = (text$: Observable<string>) =>
    text$.pipe(
      debounceTime(200),
      distinctUntilChanged(),
      map(term => term.length < 2 ? []
        : this.states.filter(v => v.toLowerCase().indexOf(term.toLowerCase()) > -1).slice(0, 10))
    )

  isValid(i:Number){
    if(i>=this.hotels.length)
      return false
    else 
      return true; 
  }


  getRandomImage(){
    return this.hotels[this.getRandomArbitrary(0,this.hotels.length-1)].filePath;
  }

 getRandomArbitrary(min, max) {
    return Math.random() * (max - min) + min;
}

  getAll(){
    this.addressService.findAll().subscribe(
      s => 
      { 
        this.addressToShow = s;
        this.addressToShow.forEach(element => {
          this.states.push(element.city);
        })
      }
    )
    this.imageService.getAll().subscribe(
      s => {
        this.hotels = s;
      }
    )
  }

  constructor(private router: Router,private route: ActivatedRoute, private addressService: AddressService, private imageService : UploadFileService) { }

  ngOnInit() {
   this.route.params.subscribe(params => {
      console.log(params['idF']);
      if(params['idF'] != null && params['idF'] === '0'){
        console.log("daa");
        window.location.reload();
        this.router.navigate(['address/1']);
      }
    });
    this.getAll();
  }

  simpleSearch(){
    if(this.checkIfInputIsValid()){
      this.accommodationSearch.beginDate = this.beginDate.year + "-" + this.beginDate.month + "-" + this.beginDate.day;
      this.accommodationSearch.endDate = this.endDate.year + "-" + this.endDate.month + "-" + this.endDate.day;
      localStorage.setItem('search', JSON.stringify(this.accommodationSearch));
      this.router.navigate(['accommodation'])
    }
  }

  checkIfInputIsValid(){
    if(this.accommodationSearch.city == ""){
      alert("Enter your destination");
      return false;
    }
    if(this.beginDate == null){
      alert("Enter begin date of your trip");
      return false;
    }
    if(this.endDate == null){
      alert("Enter end date of your trip");
      return false;
    }
    if(this.accommodationSearch.numberOfPeople == 0){
      alert("Enter number of people");
      return false;
    }
    return true;
  }
}
