import { Component, OnInit } from '@angular/core';
import { Role } from 'app/model/user/role';
import { Privilege } from 'app/model/user/privilege';
import { NgbModal, NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { PrivilegeService } from 'app/service/privilege/privilege.service';
import { RoleService } from 'app/service/role/role.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-view-privileges',
  templateUrl: './view-privileges.component.html',
  styleUrls: ['./view-privileges.component.css']
})
export class ViewPrivilegesComponent implements OnInit {

  id:number;
  privileges: Privilege[];
  newPrivilege: Privilege = new Privilege();
  modalReference: NgbActiveModal;
  dropdownSettings = {};
  p: any;

  constructor(private modalService: NgbModal, private privilegeService : PrivilegeService, private router: Router) {
    let res = localStorage.getItem('token');
    if(res == null){
      this.router.navigate(['login']);
    }
  }

  getAll(){
    this.privilegeService.findAll().subscribe(
      s => {
        this.privileges = s
      }
    );
  }

  ngOnInit() {

    this.getAll();


    this.dropdownSettings = {
      singleSelection: true,
      itemsShowLimit: 3,
      allowSearchFilter: true
    };

  }

  goToPriviegesPage(){
  this.router.navigate(['users']);
  }

  addEditPrivilege(connect : any, privilege: Privilege){
      if(privilege != null){
        this.id = privilege.id;
        this.newPrivilege.id = privilege.id;
        this.newPrivilege.name = privilege.name;
      }
      else{
        this.id = -1;
        this.newPrivilege = new Privilege();
      }
    this.modalReference = this.modalService.open(connect, { centered: true });
  }

  save(){
    if(this.id == -1){
      this.privilegeService.save(this.newPrivilege).subscribe(
        s => {
          this.privileges.push(s);
          this.modalReference.close();
        }
      )
    }
    else{
      this.privilegeService.update(this.newPrivilege).subscribe(
        s => {
          this.getAll();
          this.modalReference.close();
        }
      )
    }
  }
}
