import { Component, OnInit } from '@angular/core';
import { Chat } from 'app/model/user/chat';
import { ActivatedRoute, Router } from '@angular/router';
import { UserService } from 'app/service/user/user.service';
import { User } from 'app/model/user/user';
import { AuthService } from 'app/service/user/auth.service';
import { ChatService } from 'app/service/chat/chat.service';
import { Message } from 'app/model/user/message';

@Component({
  selector: 'app-view-inbox',
  templateUrl: './view-inbox.component.html',
  styleUrls: ['./view-inbox.component.css']
})
export class ViewInboxComponent implements OnInit {


  inbox: Chat[] = [];
  chat: Chat = new Chat();
  private sub: any;
  loggedUser: User = new User();
  newMessage: Message = new Message();

  constructor(private route: ActivatedRoute, private userService: UserService, private router: Router, private authService: AuthService, private chatService: ChatService) {
    let res = localStorage.getItem('token');
    if(res != null){
      this.loggedUser.email = this.authService.getUsername(res);
    }
   }

  ngOnInit() {
    this.userService.findByEmail(this.loggedUser.email).subscribe(
      e => {
        this.loggedUser = e;
        this.chatService.getInbox(this.loggedUser.id).subscribe(
         s => {
           this.inbox = s;
         }
        )
      })
  }

  loggedUsersMessage(message: Message){
    if(message.sender.id == this.loggedUser.id){
      return true;
    }
    return false;
  }

  sendMessage(){
    this.newMessage.date = new Date();
    this.newMessage.caption = this.chat.messages[0].caption;
    this.newMessage.opened = false;
    this.newMessage.sender = this.loggedUser;
    this.chat.messages.forEach(element =>
      {
        if(element.receiver.id != this.loggedUser.id){
          this.newMessage.receiver = element.receiver;
        }
      })
    this.chatService.sendMessage(this.chat.id, this.chat.hotelDTO.id, this.newMessage).subscribe(
      s => {
        this.chat.messages.push(this.newMessage);
        this.newMessage = new Message();
      }
    )
  }

  changeActiveChat(chat:Chat){
    this.chat=chat;
    chat.messages.forEach(element =>
      {
        element.opened = true;
      })
      chat.messages[chat.messages.length].date
  }

}
