import { Injectable } from '@angular/core';
import { environment } from 'environments/environment';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Image } from 'app/model/global-parameters/image';

@Injectable()

export class UploadFileService {

  constructor(private http: HttpClient) { }


  uploadFile(file: File): Observable<any>{
    let formdata: FormData = new FormData();
    formdata.append('file', file);
    return this.http.post(`/images/uploadFile`, formdata);
  }

  getAll(): Observable<Image[]> {
    return this.http.get<Image[]>("/images");
  }
  getDefault(): Observable<Image> {
    return this.http.get<Image>("/images/default");
  }

}
