import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { HttpClientModule } from '@angular/common/http';
import { RouterModule, Routes } from '@angular/router';
import {NgxPaginationModule} from 'ngx-pagination';

import { AppComponent } from './app.component';

import { PageNotFoundComponent } from './page-not-found/page-not-found.component';

import { HInterceptorService } from './h-interceptor.service';
import { HTTP_INTERCEPTORS } from '@angular/common/http';
import { AuthService } from './service/user/auth.service';
import { UserService } from './service/user/user.service';
import { NgbModule, NgbModalRef, NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { LoginComponent } from './component/users/login/login.component';
import { HomePageComponent } from './component/home-page/home-page.component';
import { ViewUsersComponent } from './component/users/view-users/view-users.component';
import { NgMultiSelectDropDownModule } from 'ng-multiselect-dropdown';
import { RoleService } from './service/role/role.service';
import { ViewRolesComponent } from './component/roles/view-roles/view-roles.component';
import { PrivilegeService } from './service/privilege/privilege.service';
import { ViewPrivilegesComponent } from './component/privileges/view-privileges/view-privileges.component';
import { RegistrationComponent } from './component/users/registration/registration.component';
import { PasswordValidation } from './component/users/registration/passwordValidation.directive';
import { ViewAccommodationComponent } from './component/accommodation/view-accommodation/view-accommodation.component';
import { AccommodationService } from './service/accommodation/accommodation.service';
import { ViewAddressComponent } from './component/address/view-address/view-address.component';
import { AddressService } from './service/address/address.service';
import { HotelService } from './service/hotel/hotel.service';
import { UploadFileService } from './service/image/upload-file.service';
import { UploadFileComponent } from './component/upload-file/upload-file.component';
import { ViewInboxComponent } from './component/users/view-inbox/view-inbox.component';
import { ChatService } from './service/chat/chat.service';
import { BookAccommodationComponent } from './component/accommodation/book-accommodation/book-accommodation.component';
import { MyReservationComponent } from './component/users/my-reservation/my-reservation.component';
import { ViewCommentsComponent } from './component/users/view-comments/view-comments.component';
import { RegisterAgentComponent } from './component/users/register-agent/register-agent.component';
import { UpdateAccommodationSettingsComponent } from './component/accommodation/update-accommodation-settings/update-accommodation-settings.component';
import { UserReviewService } from './service/userReview/user-review.service';



const appRoutes: Routes = [
  /*{ path: 'record/:id', component: RecordDetailsComponent },
  { path: 'main', component: MainComponent },  
  { path: '', redirectTo: 'main', pathMatch: 'full' },*/
  { path: 'login', component: LoginComponent},
  { path: 'register', component: RegistrationComponent},
  { path: 'home', component: HomePageComponent},
  { path: 'users', component: ViewUsersComponent},
  { path: 'roles', component: ViewRolesComponent},
  { path: 'privileges', component: ViewPrivilegesComponent},
  { path: 'accommodation', component: ViewAccommodationComponent},
  { path: 'address', component: ViewAddressComponent},
  { path: 'images', component: UploadFileComponent},
  { path: 'inbox', component: ViewInboxComponent},
  { path: 'myReservation', component: MyReservationComponent},
  { path: 'bookAccommodation/:accommodationId', component: BookAccommodationComponent},
  { path: 'viewComments', component: ViewCommentsComponent},
  { path: 'updateAccommodationSettings', component: UpdateAccommodationSettingsComponent},
  { path: 'registerAgent', component: RegisterAgentComponent},
  { path: '', redirectTo: 'address', pathMatch: 'full' },
  
  { path: '**', component: PageNotFoundComponent }
];

@NgModule({
  declarations: [
    AppComponent,
    PageNotFoundComponent,
    LoginComponent,
    HomePageComponent,
    ViewUsersComponent,
    ViewPrivilegesComponent,
    ViewRolesComponent,
    PasswordValidation,
    RegistrationComponent,
    ViewAccommodationComponent,
    ViewAddressComponent,
    UploadFileComponent,
    ViewInboxComponent,
    BookAccommodationComponent,
    MyReservationComponent,
    ViewCommentsComponent,
    RegisterAgentComponent,
    UpdateAccommodationSettingsComponent,
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    ReactiveFormsModule,
    FormsModule,
    HttpModule,
    HttpClientModule,
    NgxPaginationModule,
    NgMultiSelectDropDownModule.forRoot(),
    NgbModule.forRoot(),
    RouterModule.forRoot(
      appRoutes,
      { enableTracing: true, useHash: true } // <-- debugging purposes only
    )
  ],
  providers: [ //registrujem servise obaveznoo!!!!!!
    AuthService,
    UserService,
    RoleService,
    PrivilegeService,
    NgbActiveModal,
    AccommodationService,
    AddressService,
    HotelService,
    ChatService,
    UploadFileService,
    UserReviewService,
    {
      provide: HTTP_INTERCEPTORS,
      useClass: HInterceptorService,
      multi: true
    },
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
