import { Component, OnInit } from '@angular/core';
import { Accommodation } from 'app/model/accommodation/accommodation';
import { ActivatedRoute, Router } from '@angular/router';
import { NgbDateStruct } from '@ng-bootstrap/ng-bootstrap';
import { AccommodationReservation } from 'app/model/accommodation/accommodationReservation';
import { DatePipe } from '@angular/common';
import { AccommodationService } from 'app/service/accommodation/accommodation.service';
import { User } from 'app/model/user/user';
import { AuthService } from 'app/service/user/auth.service';
import { UserService } from 'app/service/user/user.service';
import { Review } from 'app/model/user/review';

@Component({
  selector: 'app-book-accommodation',
  templateUrl: './book-accommodation.component.html',
  styleUrls: ['./book-accommodation.component.css'],
  providers: [DatePipe]
})
export class BookAccommodationComponent implements OnInit {

  userReviews: Review[] = [];
  currentRate = 4;
  images = [1, 2, 3].map(() => `https://picsum.photos/900/500?random&t=${Math.random()}`);
  accommodationId: number;
  accommodationReservation: AccommodationReservation = new AccommodationReservation;
  beginDateString: string;
  endDateString: string;
  loggedUser: User = new User();

  constructor(private route: ActivatedRoute, private accommodationService: AccommodationService, public datepipe: DatePipe,
     private authService: AuthService, private userService: UserService, private router: Router) { 
    let res = localStorage.getItem('token');
    if(res != null){
      this.loggedUser.email = this.authService.getUsername(res);
    }
  }

  ngOnInit() {

    this.userService.findByEmail(this.loggedUser.email).subscribe(
      e => {
        this.loggedUser = e;
      })

    this.accommodationReservation = JSON.parse(localStorage.getItem('reservation'));
    this.beginDateString = this.datepipe.transform(this.accommodationReservation.beginDate, 'yyyy-MM-dd');
    this.endDateString = this.datepipe.transform(this.accommodationReservation.endDate, 'yyyy-MM-dd');


    this.route.params.subscribe(params => {
      if(params['accommodationId'] != null){
        this.accommodationId = +params['accommodationId'];
        this.accommodationService.getOne(this.accommodationId).subscribe(
          e => {
            this.accommodationReservation.roomDTO = e;

            this.accommodationService.getReviewsForAccommodation(this.accommodationReservation.roomDTO.id).subscribe(
              s => {
                this.userReviews = s;
              }
            )
          }
        )
     }
   });

  }

  fromModel(date: string): NgbDateStruct {

    const parsedDate = /(\d\d\d\d)-(\d\d)-(\d\d)/.exec(date);
    if (parsedDate) {
      return <NgbDateStruct>{ year: Number(parsedDate[1]), month: Number(parsedDate[2]), day: Number(parsedDate[3]) };
    } else {
      return null;
    }
  }

  book(){
    this.accommodationService.book(this.accommodationReservation.roomDTO.id, this.loggedUser.id).subscribe(
      s => {
        this.router.navigate(['myReservation']);
      }
    );
  }
}
