import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from 'environments/environment.prod';
import { Chat } from 'app/model/user/chat';
import { Observable } from 'rxjs';
import { Message } from 'app/model/user/message';

@Injectable()
export class ChatService {

  baseUrl: string = environment.baseUrl + 'message/inbox';

  constructor(private http: HttpClient) { }

  getInbox(userId: number): Observable<Chat[]>{
    return this.http.get<Chat[]>(this.baseUrl + '/' + userId);
  }

  getChat(userId:number, chatId:number): Observable<Message[]>{
    return this.http.get<Message[]>(this.baseUrl + '/' + userId + '/chat/' + chatId);
  }

  sendMessage(chatId: number, hotelId: number, message: Message): Observable<boolean>{
    return this.http.post<boolean>(this.baseUrl + '/' + chatId + "/hotel/" + hotelId, message);
  }
}
