import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from 'environments/environment.prod';
import { Observable } from 'rxjs';
import { AccomodationType } from 'app/model/global-parameters/accomodationType';
import { AdditionalService } from 'app/model/global-parameters/additionalService';
import { Hotel } from 'app/model/accommodation/hotel';

@Injectable()
export class HotelService {

  baseUrl: string = environment.baseUrl;

  constructor(private http: HttpClient) { }

  getAllHotels(): Observable<Hotel[]> {
    return this.http.get<Hotel[]>(this.baseUrl + "accommodationservice/hotels");
  }

  getAllAccomodationTypes(): Observable<AccomodationType[]> {
    return this.http.get<AccomodationType[]>(this.baseUrl + "search/accomodations");
  }

  getAllAddiionalServices(): Observable<AdditionalService[]> {
    return this.http.get<AdditionalService[]>(this.baseUrl + "search/extraoptions");
  }

  createAccommodationType(newAccommodationType: AccomodationType): Observable<AccomodationType> {
    return this.http.post<AccomodationType>(this.baseUrl + "accommodationservice/hotels/1/rooms/1/types", newAccommodationType);
  }

  createAdditionalService(newAdditionaService: AdditionalService): Observable<AdditionalService> {
    return this.http.post<AdditionalService>(this.baseUrl + "accommodationservice/options/room/1", newAdditionaService);
  }

  deleteAccommodationType(idAccommodationType: number): Observable<{}> {
    return this.http.delete(this.baseUrl + `accommodationservice/hotels/1/rooms/1/types/${idAccommodationType}`);
  }

  deleteAdditionalService(idAdditionalService: number): Observable<{}> {
    return this.http.delete(this.baseUrl + `accommodationservice/options/${idAdditionalService}`);
  }

  updateAccomodationType(accommodationType: AccomodationType): Observable<AccomodationType> {
    return this.http.put<AccomodationType>(this.baseUrl + `accommodationservice/hotels/1/rooms/1/types`, accommodationType);
  }

  updateAddiionalService(additionalService: AdditionalService): Observable<AdditionalService> {
    return this.http.put<AdditionalService>(this.baseUrl + `accommodationservice/options/room/1`, additionalService);
  }
}
