import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Privilege } from 'app/model/user/privilege';
import { Observable } from 'rxjs';
import { environment } from 'environments/environment';

@Injectable()
export class PrivilegeService {

  constructor(private http: HttpClient) { }

  baseUrl: string = environment.baseUrl + 'login';

  findOne(id: number): Observable<Privilege> {
    return this.http.get<Privilege>(this.baseUrl + `/privileges/${id}`);
  }

  findAll(): Observable<Privilege[]> {
    return this.http.get<Privilege[]>(this.baseUrl + `/privileges`);
  }

  save(privilege: Privilege): Observable<Privilege>{
    return this.http.post<Privilege>(this.baseUrl + '/privileges', privilege);
  }

  update(privilege: Privilege): Observable<Privilege>{
    return this.http.put<Privilege>(this.baseUrl + `/privileges/${privilege.id}`, privilege);
  }

}
