import { Role } from "./role";
import { Address } from "../global-parameters/address";
import { Hotel } from "../accommodation/hotel";

export class Registration{

    id: number;
    firstName: string;
    username: string;
	lastName: string;
	password: string;
    repeatPassword: string;
    adress: Address;
    email: string;
    role: Role;
    hotel: Hotel;

    constructor(){
        this.id = 0;
        this.firstName = "";
        this.username = "";
        this.lastName = "";
        this.password = "";
        this.repeatPassword = "";
        this.adress = new Address();
        this.email = "";
        this.role = new Role();
        this.hotel = new Hotel();
    }

}