import { Address } from "../global-parameters/address";
import { AdditionalService } from "../global-parameters/additionalService";
import { Image } from "../global-parameters/image";
import { PriceList } from "./priceList";

export class Hotel{
    id: number;
    rating: number;
    address: Address;
    extraOptionDTO: AdditionalService[];
    imageDTO: Image[];
    priceListDTO: PriceList[];
    constructor(){
        this.id = 0;
        this.rating = 0;
        this.address = new Address();
        this.extraOptionDTO = [];
        this.imageDTO = [];
        this.priceListDTO = [];
    }
}