import { Component, OnInit } from '@angular/core';
import { User } from 'app/model/user/user';
import { Role } from 'app/model/user/role';
import { NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { UserService } from 'app/service/user/user.service';
import { RoleService } from 'app/service/role/role.service';
import { Router } from '@angular/router';
import { Privilege } from 'app/model/user/privilege';
import { PrivilegeService } from 'app/service/privilege/privilege.service';

@Component({
  selector: 'app-view-roles',
  templateUrl: './view-roles.component.html',
  styleUrls: ['./view-roles.component.css']
})
export class ViewRolesComponent implements OnInit {

  id: number;
  privileges: Privilege[];
  newRole: Role = new Role();
  roles: Role[];
  rolePrivileges: Privilege[];
  selectedPrivilege: String;
  selectedRole: Role;
  modalReference: NgbActiveModal;
  privilegesDropDown: String[] = [];
  dropdownSettings = {};
  p: any;

  constructor(private modalService: NgbModal, private privilegeService : PrivilegeService, private roleService: RoleService, private router: Router) {
    let res = localStorage.getItem('token');
    if(res == null){
      this.router.navigate(['login']);
    }
  }

  getAll(){

    this.roleService.findAll().subscribe(
      s => {
        this.roles = s;
      }
    )
  }

  ngOnInit() {

    this.getAll();

    this.privilegeService.findAll().subscribe(
      s => {
        this.privileges = s
      }
    );

    this.dropdownSettings = {
      singleSelection: true,
      itemsShowLimit: 3,
      allowSearchFilter: true
    };

  }

  viewPrivileges(role: Role, viewConnected:any){
      this.selectedRole = role;
      this.rolePrivileges = role.privileges;
      this.modalReference = this.modalService.open(viewConnected, { centered: true });
  }

  deletePrivilege(privilege: Privilege){
    this.roleService.deletePrivilegeFromRole(this.selectedRole.id, privilege.id).subscribe(
      s =>{

        let pomPrivilege: Privilege = new Privilege();
        this.selectedRole.privileges.forEach(element => {
          if(element.id === privilege.id){
            pomPrivilege = element;
          }
        })

        this.selectedRole.privileges.splice(this.selectedRole.privileges.indexOf(pomPrivilege), 1)
      }
    );
  }

  addEditRoleAction(connect, role: Role){
    if(role != null){
      this.id = role.id;
      this.newRole.id = role.id;
      this.newRole.roleName = role.roleName;
    }
    else{
      this.id = -1;
      this.newRole = new Role();
    }
  this.modalReference = this.modalService.open(connect, { centered: true });
  }

  save(){
    if(this.id == -1){
      this.roleService.save(this.newRole).subscribe(
        s => {
          this.roles.push(s);
          this.modalReference.close();
        }
      )
    }
    else{
      this.roleService.update(this.newRole).subscribe(
        s => {
          this.getAll();
          this.modalReference.close();
        }
      )
    }
  }

  goToPriviegesPage(){
  this.router.navigate(['privileges']);
  }

  openAddingPrivilegePopup(connect, role: Role){
    this.privilegesDropDown = [];
    this.selectedPrivilege = "";
    this.selectedRole = role;
    this.privileges.forEach(privilege => {
          let hasPrivilege = false;
          role.privileges.forEach(rolePrivilege => {
            if(privilege.id == rolePrivilege.id){
              hasPrivilege = true;
            }
          }
        )
        if(!hasPrivilege){
          this.privilegesDropDown.push(privilege.name);
        }
      }
    )
    this.modalReference = this.modalService.open(connect, { centered: true });
  }

  addPrivilegeAction(){
    let privilegeToAdd = this.privileges.find(element => element.name == this.selectedPrivilege);
    this.roleService.addPrivilegeToRole(this.selectedRole.id, privilegeToAdd.id).subscribe(
      s => {
        this.selectedRole.privileges.push(privilegeToAdd)
        this.modalReference.close()
      }
    );
  }
}
