import { Role } from "./role";
import { Address } from "../global-parameters/address";
import { Hotel } from "../accommodation/hotel";
import { AccommodationReservation } from "../accommodation/accommodationReservation";

export class User{

    id: number;
	firstName: string;
	lastName: string;
	email: string ;
    role: Role[];
    username: string;
    active: boolean;
    adress: Address;
    workCertificateNumber: string;
    hotelDTO: Hotel;
    roomReservationDTO: AccommodationReservation[];
    
    constructor(){
        this.id = 0;
        this.firstName = ""
        this.lastName = "";
        this.email = "";
        this.role = [];
        this.username = "";
        this.active = true;
        this.adress = new Address();
        this.hotelDTO = new Hotel();
        this.roomReservationDTO = [];
        this.workCertificateNumber = "";
    }

}