import { Hotel } from "./hotel";
import { AccomodationType } from "../global-parameters/accomodationType";
import { Image } from "../global-parameters/image";

export class Accommodation{
    id: number;
    description: string;
    cancellationDays: number;
    cancellationAllowed: boolean;
    capacity: number;
    accomodationTypeDTO: AccomodationType;
    numberOfBeds: number;
    hotelDTO: Hotel;
    currentlyPrice: number;
    imageDTO: Image[];

    constructor(){
        this.id = 0;
        this.description = "";
        this.cancellationAllowed = true;
        this.cancellationDays = 0;
        this.capacity = 0;
        this.numberOfBeds = 0;
        this.hotelDTO = new Hotel();
        this.currentlyPrice = 0;
        this.accomodationTypeDTO = new AccomodationType();
        this.imageDTO = [];
        let image1 = new Image();
        this.imageDTO.push(image1);
    }
}