import { Accommodation } from "./accommodation";

export class AccommodationReservation{
    id: number;
    beginDate: Date;
    endDate: Date;
    realised: boolean;
    roomDTO: Accommodation;
    allowedCancel: boolean
    price: number;

    constructor(){
        this.id = 0;
        this.beginDate = new Date();
        this.endDate = new Date();
        this.realised = false;
        this.roomDTO = new Accommodation();
        this.price = 0;
        this.allowedCancel = false;
    }
}