import { Accommodation } from "../accommodation/accommodation";
import { User } from "./user";

export class Review{
    id: number;
    rating: number;
    comment: string;
    timeStamp: Date;
    roomDTO: Accommodation;
    systemUserInfoDTO: User;
    approved: boolean;

    constructor(){
        this.id = 0;
        this.rating = 0;
        this.comment = "";
        this.timeStamp = new Date();
        this.roomDTO = new Accommodation();
        this.systemUserInfoDTO = new User();
        this.approved = false;
    }
}