import { Component, OnInit } from '@angular/core';
import { AccomodationType } from 'app/model/global-parameters/accomodationType';
import { AdditionalService } from 'app/model/global-parameters/additionalService';
import { HotelService } from 'app/service/hotel/hotel.service';

@Component({
  selector: 'app-update-accommodation-settings',
  templateUrl: './update-accommodation-settings.component.html',
  styleUrls: ['./update-accommodation-settings.component.css']
})
export class UpdateAccommodationSettingsComponent implements OnInit {

  accommodationTypes: AccomodationType[] = []
  additionalServices: AdditionalService[] = []
  newAccommodationType: AccomodationType = new AccomodationType();
  newAdditionalService: AdditionalService = new AdditionalService();
  constructor(private hotelService: HotelService) {
   }

  ngOnInit() {
    this.hotelService.getAllAccomodationTypes().subscribe(
      s =>{
        this.accommodationTypes = s;
      }
    )
    this.hotelService.getAllAddiionalServices().subscribe(
      s =>{
        this.additionalServices = s;
      }
    )
  }

  addAccommodationType(){
    if(this.newAccommodationType.id == 0){
      this.hotelService.createAccommodationType(this.newAccommodationType).subscribe(
        s => {
          this.accommodationTypes.push(s);
        }
      )
    }
    else{
      this.hotelService.updateAccomodationType(this.newAccommodationType).subscribe(
        s => { 
          this.accommodationTypes.forEach(element =>{
            if(element.id == s.id){
              element.name = s.name;
            }
          }
        )}
      )
    }
    this.newAccommodationType = new AccomodationType();
  }

  deleteAccommodationType(accommodationType: AccomodationType){
    this.hotelService.deleteAccommodationType(accommodationType.id).subscribe(
      s => {
          let pomAccommodationType: AccomodationType = new AccomodationType();
          this.accommodationTypes.forEach(element => {
            if(element.id === accommodationType.id){
              pomAccommodationType = element;
            }
          })

          this.accommodationTypes.splice(this.accommodationTypes.indexOf(pomAccommodationType), 1)
      }
    )
  }

  editAccommodationType(accommodationType: AccomodationType){
    this.newAccommodationType.name = accommodationType.name;
    this.newAccommodationType.id = accommodationType.id
  }

  addAdditionalService(){
    if(this.newAdditionalService.id == 0){
      this.hotelService.createAdditionalService(this.newAdditionalService).subscribe(
        s => {
          this.additionalServices.push(s);          
        }
      )
    }
    else{
      this.hotelService.updateAddiionalService(this.newAdditionalService).subscribe(
        s => {
          this.additionalServices.forEach(element =>{
            if(element.id == s.id){
              element.name = s.name;
            }
          }
        )        
        }
      )
    }
    this.newAdditionalService = new AdditionalService();
  }

  deleteAdditionalService(additionalService: AdditionalService){
    this.hotelService.deleteAdditionalService(additionalService.id).subscribe(
      s => {
        let pomAdditionalService: AdditionalService = new AdditionalService();
        this.additionalServices.forEach(element => {
          if(element.id === additionalService.id){
            pomAdditionalService = element;
          }
        })
    
        this.additionalServices.splice(this.additionalServices.indexOf(pomAdditionalService), 1)
      }
    )
  }

  editAdditionalService(additionalService: AdditionalService){
    this.newAdditionalService.id = additionalService.id;
    this.newAdditionalService.name = additionalService.name;
  }

}
