import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from 'environments/environment';
import { Observable } from 'rxjs';
import { Review } from 'app/model/user/review';

@Injectable()
export class UserReviewService {

  baseUrl: string = environment.baseUrl + 'rating/rate';

  constructor(private http: HttpClient) { }

  getUnapprovedUserReviews(): Observable<Review[]>{
    return this.http.get<Review[]>(this.baseUrl);
  }

  getUserReviews(userId: number): Observable<Review[]>{
    return this.http.get<Review[]>(this.baseUrl + `/user/${userId}`);
  }

  updateReview(userReview: Review): Observable<Review>{
    return this.http.put<Review>(this.baseUrl + "/" + userReview.id, userReview);
  }

  createReview(userReview: Review): Observable<Review>{
    return this.http.post<Review>(this.baseUrl, userReview);
  }

  deleteReview(userReview: Review): Observable<boolean>{
    return this.http.delete<boolean>(this.baseUrl + "/" + userReview.id);
  }
}
