import { Component, OnInit } from '@angular/core';
import { Review } from 'app/model/user/review';
import { UserReviewService } from 'app/service/userReview/user-review.service';
import { AccommodationService } from 'app/service/accommodation/accommodation.service';

@Component({
  selector: 'app-view-comments',
  templateUrl: './view-comments.component.html',
  styleUrls: ['./view-comments.component.css']
})
export class ViewCommentsComponent implements OnInit {

  commentsToApprove: Review[] = [];

  constructor(private userReviewService: UserReviewService, private accommodationService: AccommodationService) { }

  ngOnInit() {
    this.userReviewService.getUnapprovedUserReviews().subscribe(
      s => {
        this.commentsToApprove = s;
      }
    )
  }

  approveComment(review: Review){
    if(confirm("Approve comment?")) {
      review.approved = true;
      this.userReviewService.updateReview(review).subscribe(
        s => {
          let pomReview: Review = new Review();
          this.commentsToApprove.forEach(element => {
            if(element.id === review.id){
              pomReview = element;
            }
          })
      
          this.commentsToApprove.splice(this.commentsToApprove.indexOf(pomReview), 1)
          this.accommodationService.updateRating(review.roomDTO.id).subscribe(
            s => {
              console.log("USpeo");
            }
          );
        }
      )
    }
  }

  deleteComment(review: Review){
    if(confirm("Delete comment?")) {
      this.userReviewService.deleteReview(review).subscribe(
        s => {
          let pomReview: Review = new Review();
          this.commentsToApprove.forEach(element => {
            if(element.id === review.id){
              pomReview = element;
            }
          })
      
          this.commentsToApprove.splice(this.commentsToApprove.indexOf(pomReview), 1)
        }
      )
    }
  }

}
