export class Address{
    country: string;
    city: string;
    street: string;
    streetNumber: number;
    coordinateX: number;
    coordinateY: number;

    constructor(){
        this.country = "";
        this.city = "";
        this.street = "";
        this.streetNumber = 0;
        this.coordinateX = 0;
        this.coordinateY = 0;
    }

}