import { User } from "./user";

export class Message{
    id: number;
    caption: string;
    text: string;
    opened: boolean;
    date: Date;
    sender: User;
    receiver: User;

    constructor(){
        this.id = 0;
        this.caption = "";
        this.text = "";
        this.opened = false;
        this.date = new Date();
        this.sender = new User();
        this.receiver = new User();
    }
}