import { Component, OnInit } from '@angular/core';
import { Registration } from 'app/model/user/registration';
import { AuthService } from 'app/service/user/auth.service';
import { Router } from '@angular/router';
import { Address } from 'app/model/global-parameters/address';
import { AddressService } from 'app/service/address/address.service';
import { HotelService } from 'app/service/hotel/hotel.service';
import { Hotel } from 'app/model/accommodation/hotel';

@Component({
  selector: 'app-register-agent',
  templateUrl: './register-agent.component.html',
  styleUrls: ['./register-agent.component.css']
})
export class RegisterAgentComponent implements OnInit {

  user: Registration = new Registration();
  maxLength: number = 30;
  mailNotUnique: boolean = true;
  addressToShow: Hotel[] = [];
  states: string[] = [];
  selectedState: string = "";
  dropdownSettings;

  patternHigh: any = "^(?=.*[a-z])(?=.*[A-Z])(?=.*\\d)(?=.*[@$!%*?&])[A-Za-z\\d@$!%*?&]{12,}$";

  getAll(){
    this.hotelService.getAllHotels().subscribe(
      s => 
      { 
        this.addressToShow = s;
        let pom: string[] = [];
        this.addressToShow.forEach(element => {
          pom.push(element.address.country);
        })
        this.states = pom;
      }
    )
  }

  constructor(private auth: AuthService,
              private router: Router, private hotelService: HotelService) {
    this.user = new Registration();
    this.user.adress = new Address();
  }

  ngOnInit() {
    this.getAll();
    this.dropdownSettings = {
      singleSelection: true,
      itemsShowLimit: 3,
      allowSearchFilter: true
    };
  }


  register(){
    let message: string = this.proveriUnosKorisnik();
    this.user.repeatPassword = this.user.password;
    if(message === "OK")
    {
      this.addressToShow.forEach(element =>
        {
          if(element.address.country == this.selectedState){
            this.user.hotel = element;
          }
        })
      this.auth.signup(this.user)
          .subscribe(
            res => 
            {
              this.router.navigate(['login'])
            },
            err => {
              console.log("usao kao error");
              if (err.status === 400) { // bad request losi kredencijali
                console.log("usao 400");
                this.mailNotUnique = false;
            }
          }
        )
    }
    else{
      alert(message);
    }
  }

  validateEmail(email: string) 
  {
    let re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(String(email).toLowerCase());
  }
  

  validatePasswords(){
    if(this.user.password === this.user.repeatPassword){
      return true;
    }
    else{
      return false;
    }
  }

proveriUnosKorisnik(): string{
  if(this.user.email === "" || this.user.email.length > 60){
    return "Morate popuniti validnu e-mail adresu koja mora biti kraca od " + 60 + " karaktera";
  }

  if(!this.validateEmail(this.user.email)){
    return "Morate uneti validnu e-mail adresu";
  }
  
  if(this.user.password === "" || this.user.password.length > this.maxLength){
    return "Morate popuniti lozinku koja mora biti kraca od " + this.maxLength + " karaktera";
  }

  if(this.user.firstName === "" || this.user.firstName.length > this.maxLength){
    return "Morate popuniti ime koja mora biti kraca od " + this.maxLength + " karaktera";
  }

  if(this.user.lastName === "" || this.user.lastName.length > this.maxLength){
    return "Morate popuniti prezime koja mora biti kraca od " + this.maxLength + " karaktera";
  }

  if(this.user.adress.country === "" || this.user.lastName.length > this.maxLength){
    return "Morate popuniti drzavu koja mora biti kraca od " + this.maxLength + " karaktera";
  }

  if(this.user.adress.city === "" || this.user.lastName.length > this.maxLength){
    return "Morate popuniti grad koja mora biti kraca od " + this.maxLength + " karaktera";
  }

  if(this.user.adress.street === "" || this.user.lastName.length > this.maxLength){
    return "Morate popuniti ulicu koja mora biti kraca od " + this.maxLength + " karaktera";
  }

  if(this.user.adress.streetNumber === null || this.user.lastName.length > this.maxLength){
    return "Morate popuniti broj ulice koja mora biti kraca od " + this.maxLength + " karaktera";
  }

  if(this.user.adress.coordinateX === null ||  180< this.user.adress.coordinateX || this.user.adress.coordinateY <-180 ){
    return "Morate popuniti coodrinate x koja mora biti kraca izmedju 180 i -180";
  }
  
  if(this.user.adress.coordinateY === null || 90< this.user.adress.coordinateY || this.user.adress.coordinateY <-180 ){
    return "Morate popuniti coodrinate y koja mora biti kraca izmedju 90 i -90";
  }

  //if(this.selectedState === ""){
    //return "Morate izabrati hotel"
  //}

  return "OK";
}

}
