import { Component, OnInit } from '@angular/core';
import { UploadFileService } from 'app/service/image/upload-file.service';
import { Image } from 'app/model/global-parameters/image';

@Component({
  selector: 'app-upload-file',
  templateUrl: './upload-file.component.html',
  styleUrls: ['./upload-file.component.css']
})
export class UploadFileComponent implements OnInit {

  selectedFiles: FileList;
  currentFileUpload: File;
  images: Image[] = [];

  constructor(private uploadFileService: UploadFileService) { }

  ngOnInit() {
    this.uploadFileService.getAll().subscribe(
      succ => {
        this.images = succ;
    });
  }

  selectFile(event) {
    this.selectedFiles = event.target.files;
  }


  upload() {
    this.currentFileUpload = this.selectedFiles.item(0)
    this.uploadFileService.uploadFile( this.currentFileUpload)
    .subscribe(event => {
      //alert("uspeo")
      this.uploadFileService.getAll().subscribe(
        succ => {
          this.images = succ;
      });
    });
   
    this.selectedFiles = undefined;
  }


}
