import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { UserService } from 'app/service/user/user.service';
import { NgbModal, NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { User } from 'app/model/user/user';
import { Role } from 'app/model/user/role';
import { RoleService } from 'app/service/role/role.service';

@Component({
  selector: 'app-view-users',
  templateUrl: './view-users.component.html',
  styleUrls: ['./view-users.component.css']
})
export class ViewUsersComponent implements OnInit {

  users: User[];
  // newModule: SystemModule = new SystemModule();
  allRoles: Role[];
  userRoles: Role[];
  selectedUser: User;
  selectedRole: String;
  modalReference: NgbActiveModal;
  rolesDropDown: String[] = [];
  // closeResult: string;
  dropdownSettings = {};
  // dropdownSettingsSearch = {};
  // selectedForSearch: String[] = []; 
  // endDate: Date = new Date();
  p: any;

  constructor(private modalService: NgbModal, private userService : UserService, private roleService: RoleService, private router: Router) {
    let res = localStorage.getItem('token');
    if(res == null){
      this.router.navigate(['login']);
    }
  }

  ngOnInit() {

    this.userService.findAll().subscribe(
      s => {
        this.users = s
      }
    );

    this.roleService.findAll().subscribe(
      s => {
        this.allRoles = s;
      }
    )


    this.dropdownSettings = {
      singleSelection: true,
      itemsShowLimit: 3,
      allowSearchFilter: true
    };

  }

  //   this.dropdownSettingsSearch = {
  //     singleSelection: false,
  //     itemsShowLimit: 2,
  //     allowSearchFilter: true
  //   };
  // }

  // open(content) {
  //   this.newModule = new SystemModule();
  //   this.modalReference = this.modalService.open(content, { centered: true });
  // }

  viewRoles(user: User, viewConnected:any){
      this.selectedUser = user;
      this.userRoles = user.role;
      this.modalReference = this.modalService.open(viewConnected, { centered: true });
  }

  deleteRole(role: Role){
    this.userService.deleteRoleFromUser(this.selectedUser.id, role.id).subscribe(
      s =>{

        let pomRole: Role = new Role();
        this.selectedUser.role.forEach(element => {
          if(element.id === role.id){
            pomRole = element;
          }
        })

        this.selectedUser.role.splice(this.selectedUser.role.indexOf(pomRole), 1)
      }
    );
  }

  // save(){
  //   this.moduleService.save(this.newModule).subscribe(
  //     s => {
  //       this.modules.push(s);
  //       this.modalReference.close();
  //     }
  //   )
  // }

  goToRolesPage(){
    this.router.navigate(['roles']);
  }

  openAddingRolePopup(connect, user: User){
    this.rolesDropDown = [];
    this.selectedRole = "";
    this.selectedUser = user;
    this.allRoles.forEach(role => {
          let hasRole = false;
          user.role.forEach(userRole => {
            if(role.id == userRole.id){
              hasRole = true;
            }
          }
        )
        if(!hasRole){
          this.rolesDropDown.push(role.roleName);
        }
      }
    )
    this.modalReference = this.modalService.open(connect, { centered: true });
  }

  addRoleAction(){
    let roleForAdd = this.allRoles.find(element => element.roleName == this.selectedRole);
    this.userService.addRoleToUser(this.selectedUser.id, roleForAdd.id).subscribe(
      s => {
        this.selectedUser.role.push(roleForAdd)
        this.modalReference.close()
      }
    );
  }
}
